$black: #151515;
$red: #FF2C2C;

.Form {
    margin: 0 auto;
    width: 85%;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-flow: column;

    .Section {
        margin-bottom: 1em;
            
        width: 100%;

        display: flex;
        justify-content: center;

        flex-flow: column;

        .Title {
            font-family: 'Staatliches', cursive;
            font-size: 1.5em;
            margin-bottom: 0.2em;
        }
    }

    .Button {
        width: 100%;
        height: 3em;

        font-family: 'Poppins', sans-serif;
        font-size: 1.125em;
        font-weight: 500;
        text-transform: uppercase;
        color: white;

        border: none;
        background: none;

        background-color: $red;

        &:disabled {
            background-color: #F2F2F2;
        }
    }

    .SubmitError {
        font-size: 1em;
        color: $red;

        margin: 1em auto 0;
    }
}

@media (min-width: 720px) {
    .Form {
        width: 75%;
        font-size: 1.2em;
    }
}

@media (min-width: 1025px) {
    .Form {
        width: 45%;
        font-size: 1em;
    }
}

