$black: #151515;

.Preview {
    width: 85%;
    margin: 0 auto;

    .Meta {
        width: 100%;

        display: flex;
        align-items: center;

        margin-bottom: 0.5em;

        .Description {
            width: 50%;

            font-family: 'Staatliches', cursive;
            font-size: 1.5em;
            color: $black;

            &:nth-child(2n) {
                display: none;
            }
        }
    }

    .Message {
        width: 100%;
        height: 3em;

        margin-bottom: 0.75em;

        border: 0.5px solid rgba(0, 0, 0, 0.25);

        display: flex;
        justify-content: center;
        align-items: center;

        transition: all 0.15s;
        
        &:nth-child(2n) {
            background-color: #F2F2F2;
        }

        &:hover {
            background-color: darken(white, 8);
            box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
            cursor: pointer;
        }

        .Info {
            width: 50%;

            &:nth-child(1) {
                .Text {
                    border: none;
                }
            }

            .Text {
                font-weight: 300;
                font-size: 1.125em;
                text-overflow: ellipsis;

                color: $black;
                
                overflow: hidden;
                white-space: nowrap;

                width: 80%;
                padding: 0.5em 0 0.5em 0.75em;

                border-left: 0.5px solid rgba(0, 0, 0, 0.25);
            }
            
            &:nth-child(2n) {
                display: none;
            }
        }
    }

    .Total {
        font-family: 'Staatliches', cursive;
        font-size: 1.5em;
        color: $black;
        
        margin-top: 0.5em;
        padding-bottom: 1em;
    }
}

@media (min-width: 720px) {
    .Preview {
        width: 75%;

        .Meta {
            .Description {
                width: 33%;

                &:nth-child(4) {
                    display: block;
                }
            }
        }

        .Message {
            .Info {
                width: 33%;

                &:nth-child(4) {
                    display: block;
                }
            }
        }
    }
}

@media (min-width: 1024px) {
    .Preview {
        width: 75%;

        .Meta {
            .Description {
                width: 25%;

                &:nth-child(2) {
                    display: block;
                }
            }
        }

        .Message {
            height: 4em;

            .Info {
                width: 25%;

                &:nth-child(2) {
                    display: block;
                }
            }
        }
    }
}
